





























export default {
	data() {
		return {
			isLogin: false,
			name: "",
			logoImage:'',
			guide: [],
		}
	},
	onLoad() {
		let state = this.common.get("state")
		if (state == 1) {
			uni.reLaunch({
				url: "/pages/login/login"
			})
			return
		} else if (state == 2) {
			let that = this
			that.isLogin = true
		} else {
			// this.name = "云仓储"
			uni.switchTab({
				url: "/pages/user/user"
			})
		}
	},
	onShow() {
	    let uid = this.common.get("uid")
	    if (uid != "") {
	        this.http({
	            url: this.api.merchant,
	            method: "POST",
	            data: {
	                uid: uid
	            },
	            success: res => {
	                this.name = res.shop_name
					this.logoImage = res.shop_logo
	            }
	        })
			this.index_list()
	    } else {
			uni.switchTab({
			    url: "/pages/user/user"
			})
		}
	},
	methods: {
		// 主页功能列表接口
		index_list() {
			this.http({
				url: this.api.Index_index,
				method: "POST",
				data: {
					uid: this.common.get("uid")
				},
				success: res => {
					this.guide = res
				}
			})
			
		},
		// 页面跳转
		jump(item) {
			console.log('item',item)
			// 客户管理
			if(item == 35) {
				uni.navigateTo({
					url: "/pages/customer/customer/customer"
				})
			}
			
		}
	}
}
